#app2 {
}
#app2 .tab-bar {
	display: flex;
}
#app2 .tab-bar > li {
	border: 1px solid grey;
	width: 50%;
	text-align: center;
}
#app2 .tab-bar > li.selected {
	background: gray;
	color: white;
}
#app2 .tab-content {
	display: flex;
}
#app2 .tab-content > li {
	display: none;
}
#app2 .tab-content > li.active {
	display: block;
}
